<template>
    <div class="city-list" ref="wrapper">
        <div class="box">
            <div class="current">
                <h2 class="title">当前城市</h2>
                <div class="button-list">
                    <div class="button-wrapper">
                        <div class="button">
                            {{this.$store.state.city}}
                        </div>
                    </div>
                </div>
            </div>

            <div class="hot">
                <h2 class="title">热门城市</h2>
                <div class="button-list">
                    <div class="button-wrapper"  
                    v-for="(item,index) in hot" 
                    :key="index"
                    @click="handleChangeCity(item.name)"
                    >
                        <div class="button">{{item.name}}</div>
                    </div>
                </div>
            </div>

             <div class="list">
                <h2 class="title">城市列表</h2>
                <div class="list" v-for="(item,index) in cities" :key="index" :ref="index">
                    <div class="title border-topbottom">{{index}}</div>
                    <div class="item-list">
                        <div class="item border-topbottom" 
                        v-for="innerItem in item" 
                        :key="innerItem.id"
                        @click="handleCityClick(innerItem.name)"
                        >
                            {{innerItem.name}}
                        </div>
                        
                    </div>
                </div>
            </div>

            
        </div>
    </div>
</template>

<script>
import BScroll from 'better-scroll'
export default {
    name:"CityList",
    props:{
        cities:Object,
        hot:Array,
        letter:String
    },
    mounted(){
        let wrapper = this.$refs.wrapper
        this.scroll = new BScroll(wrapper)
    },
    watch:{
        letter(){
            this.scroll.scrollToElement(this.$refs[this.letter][0])
        }
    },
    methods:{
        handleCityClick(city){
            this.$store.commit("changeCity",city)
        }
    }
}
</script>

<style scoped>
    .title{
        line-height: 30px;
        background-color: #eee;

    }
    .button-list{
        display: flex;
        flex-wrap: wrap;
    }
    .button-wrapper{
        width: 33.33%;
        color: #fff;
    }
    .button{
        box-sizing: border-box;
        margin: 10px 15px;
        padding: .1rem 0 ;
        text-align: center;
        border-radius: .06rem;
        border: .02rem solid #ccc;
    }
    .item{
        line-height: .76rem;
        padding-left: .2rem;
    }
    .city-list{
        overflow: hidden;
        position: absolute;
        left: 0;
        top: 80px;
        right: 0;
        bottom: 0;
        z-index: -999;

    }
</style>


