<template>
    <div class="alphabet">
        <ul class="list">
            <li class="item" v-for = "(item,key) in cities" 
            :key="key" 
            :ref="key"
            @click = "handleLetter"
            @touchstart.prevent = "handleTouchStart"
            @touchmove = "handleTouchMove"
            @touchend = "handleTouched"
            >
                {{key}}
            </li>
        </ul>
    </div>
</template>
<script>
export default {
    
    name:"CityAlphabet",
    props:{
        cities:Object
    },
    computed:{
        letters(){
            let arr = [];
            for(let key in this.cities){
                arr.push(key)
            }
            return arr
        }
    },
    updated(){
        this.startY = this.$refs['A'][0].offsetTop
    },
    methods:{
        handleLetter(event){
            this.$emit('change',event.target.innerText)
        },
        handleTouchStart(){
            this.touchStatus = true
        },
        handleTouchMove(evevt){
            if(this.touchStatus){
                if(this.timer){
                    clearTimeout(this.timer)
                }
                this.timer = setTimeout(()=>{
                    const touchY = evevt.touches[0].clientY - 150;
                    const index = Math.floor((touchY - this.startY)/20)
                    console.log(index)
                    if(index>=0&&index<this.letters.length){
                        this.$emit('change',this.letters[index])
                    }
                })
               
            }
        },
        handleTouched(){
            this.moveStatus = false
        }
    }
}
</script>

<style scoped>
    .alphabet{
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
    .list{
        position:absolute;   
        right:10px;
        top:150px;
        bottom:0      
    }
    .item{
        width:25px;
        line-height:20px;
        text-align: center;
        color: #666;
    }

</style>


