<template>
    <div>
        <div class="search">
            <input type="text" v-model = "keyword" class="search-input" placeholder="输入城市名或者拼音">
        </div>
        <div class="search-content" v-show="keyword">
            <ul>
                <li class="search-item border-bottom" 
                v-for="item of list" 
                :key="item.id"
                @click="handleCityClick(item.name)"
                >
                    {{item.name}}
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
export default {
    name:"CitySearch",
    data(){
        return{
            keyword:"",
            list:[],
            timer:null
        }
    },
    props:{
        cities:Object
    },
    watch:{
        keyword(){
            var arr = []
            if(this.timer){
                clearTimeout(this.timer)
            }
            this.timer = setTimeout(()=>{
                for(let key in this.cities){
                    this.cities[key].forEach(element => {
                    if(element.name.indexOf(this.keyword)>-1||element.spell.indexOf(this.keyword)>-1){
                        arr.push(element)
                    } 
                    });
                }
                this.list = arr
            },1000)
            
        }
    },
    methods:{
        handleCityClick(city){
            this.$store.commit('changeCity',city)
            this.$router.push('/')
        }
    }
}
</script>

<style scoped>
    .search{
        height: 40px;
        background-color: #00bcd4;
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding: 0 10px;
    }
    .search-input{
        height: 30px;
        line-height: 30px;
        width:100%;
        border-radius: 5px;
        box-sizing: border-box;
        text-align: center;
    }
    .search-content{
        position: absolute;
        left: 0;
        right:0;
        top: 80px;
        bottom: 0;
        background-color: #fff;
    }
</style>


