<template>
    <div class="iconList">
        <mt-swipe :auto="0">
            <mt-swipe-item v-for="(page,index) of pages" :key="index">
                <div class="icon" v-for="item of page" :key="item.id">
                    <div class="icon-img">
                        <img :src="item.imgUrl" alt="">
                    </div>
                    <div class="icon-desc">{{item.desc}}</div>
                </div>
            </mt-swipe-item>  
        </mt-swipe>
    </div>
</template>
<script>
import Vue from 'vue'
export default {
    name:"HomeIconList",
    props:{
        list:Array
    },
    computed:{
        pages(){
            let pages = [];
            this.list.forEach((value,index)=>{
                //console.log(value)
                //console.log(index)
                var page = Math.floor(index/8);
                if(!pages[page]){
                    pages[page] = [];
                }
                pages[page].push(value);
            })
            //console.log(pages)
            return pages;
        }
    }
}
</script>
<style scoped>
    .iconList{
        height: 250px;
    }
    .icon{
        width: 25%;
        float: left;
        box-sizing: border-box;
        text-align: center;
        padding: 10px;
    }
    .icon-img img{
        width: 100%;
    }
    .icon-desc{
        margin-top:10px ;
    }
</style>


