<template>
    <div class="home">
        <home-header></home-header>
        <home-swipper :list="swipeList"></home-swipper>
        <home-icon-list :list="iconList"></home-icon-list>
        <home-recommend :list="recommendList"></home-recommend>
        <home-weekend :list="weekendList"></home-weekend>
    </div>
</template>

<script>
import {mapState} from 'vuex'
import HomeHeader from './components/Header'
import axios from 'axios'
import HomeSwipper from './components/Swipper'
import HomeIconList from './components/IconList'
import HomeRecommend from './components/Recommend'
import HomeWeekend from './components/Weekend'
export default {
    name:"Home",
    components:{
        HomeHeader,
        HomeSwipper,
        HomeIconList,
        HomeRecommend,
        HomeWeekend
    },
    data(){
        return{
            swipeList:[],
            iconList:[],
            recommendList:[],
            weekendList:[]
        }
    },
    computed:{
        ...mapState(['city'])
    },
    mounted(){
        this.getHomeData();
    },
    activated(){
        if(this.lastCity !== this.city){
            this.lastCity = this.city
            thsi.getHomeData()
        }
    },
    methods:{
        getHomeData(){
            axios.get("/tour/static/api/index.json")
            .then((res) =>{
                console.log(res.data.data)
                var data = res.data.data;
                this.swipeList = data.swiperList
                this.iconList = data.iconList
                this.recommendList = data.recommendList
                this.weekendList =data.weekendList
                //console.log(this.swipeList)
                //console.log(this.iconList)
            })
        }
    }
}
</script>
<style scoped>

</style>


