<template>
    <div class="recommend">
        <h3 class="title1">热销推荐</h3>
        <div class="content-list">
            <mt-swipe :auto="4000">
                <mt-swipe-item v-for="item of list" :key="item.id">
                    <div class="left">
                        <img :src="item.imgUrl" alt="">
                    </div>
                    <div class="right">
                        <p class="title">{{item.title}}</p>
                        <p class="desc">{{item.desc}}</p>
                        <a href="" class="detail">查看详情</a>
                    </div>
                </mt-swipe-item>  
            </mt-swipe>
        </div>
    </div>
</template>


<script>
export default {
    name:"HomeRecommend",
    props:{
        list:Array
    }
    
}
</script>
<style scoped>
    .title1{
        height: 40px;
        background-color: #eee;
        line-height: 40px;
        text-indent: 1em;
    }
    .content-list{
        height: 135px;

    }
    .content{
        display: flex;
        justify-content: space-between;
        padding: 5px;

    }
    .left img{
        width: 100px;
        height: 100px;
        float: left;
        padding: 5px;
    }
    .right{
        float: left;
        padding-left: 15px;
    }
    .right .title{
        font-size: 16px;
        padding: 10px 0;
    }
    .desc{
        color:#ccc;
        padding: 10px 0;

    }
    .detail{
        color: #fff;
        background-color: orange;
        display: inline-block;
        width: 100px;
        padding: 10px 0;
        border-radius: 10px;
        text-align: center;
    }
</style>


